import React from "react";
import PropTypes from "prop-types";
import { Secondary } from "../story/Button.stories";
import "../index.css";
import clsx from "clsx";

/**
 * Primary UI component for user interaction
 */
export const Button = ({ primary, backgroundColor, size, label, ...props }) => {
  // const mode = primary ? "storybook-button--primary" : "storybook-button--secondary";
  const getButtonSize = (size) => {
    switch (size) {
      case "small":
        return "size-sm";
      case "large":
        return "size-lg";
      default:
        return "size-md";
    }
  };

  const base = "rounded-md bg-black px-3.5 py-2.5 text-sm font-semibold text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 hover:bg-gray-50";
  const styles = clsx({ "bg-white": primary }, getButtonSize, base);
  return (
    // <button
    //   type="button"
    //   className={['storybook-button', `storybook-button--${size}`, mode].join(' ')}
    //   style={backgroundColor && { backgroundColor }}
    //   {...props}
    // >
    //   {label}
    // </button>
    <button type="button" className={styles}>
      Button text
    </button>
  );
};

Button.propTypes = {
  /**
   * Is this the principal call to action on the page?
   */
  primary: PropTypes.bool,
  /**
   * What background color to use
   */
  backgroundColor: PropTypes.string,
  /**
   * How large should the button be?
   */
  size: PropTypes.oneOf(["small", "medium", "large"]),
  /**
   * Button contents
   */
  label: PropTypes.string.isRequired,
  /**
   * Optional click handler
   */
  onClick: PropTypes.func,
};

Button.defaultProps = {
  backgroundColor: null,
  primary: false,
  size: "medium",
  onClick: undefined,
};

